import {
  Wrapper,
  List,
  Item,
  IconBtn,
  TitleList,
  TitleItem,
  WrapperDesc,
  Table,
  TD,
  THead,
} from './AdminOrders.styled';
import { useEffect, useState, Fragment } from 'react';
import * as API from 'services/api';
import toast from 'react-hot-toast';
import { UilEye } from '@iconscout/react-unicons';

export function AdminOrders() {
  const [orders, setOrders] = useState([]);
  const [clickedOrder, setClickedOrder] = useState(false);

  const transformDate = dateString => {
    const date = new Date(dateString);
    const formattedDate = `${date.getDate()}/${
      date.getMonth() + 1
    }/${date.getFullYear()}`;
    const formattedTime = `${date.getHours()}:${
      date.getMinutes() < 10 ? '0' : ''
    }${date.getMinutes()}`;

    return `${formattedDate} ${formattedTime}`;
  };

  useEffect(() => {
    (async () => {
      try {
        const orders = await API.getOrders('/orders');
        console.log(orders);
        setOrders(orders);
      } catch (error) {
        console.log(error);
        toast.error('   ,  ');
      }
    })();
  }, []);

  return (
    <Wrapper>
      <List>
        {orders &&
          orders.map(
            ({
              _id,
              code,
              date,
              totalPrice,
              contacts: { firstName, lastName, phone },
              products,
              orderDesc: { address, city, deliveryType, department, payment },
            }) => {
              const dateTransformed = transformDate(date);

              return (
                <Fragment key={_id}>
                  <Item>
                    <TitleList>
                      <TitleItem>{code}</TitleItem>
                      <TitleItem>{totalPrice}</TitleItem>
                      <TitleItem>{dateTransformed}</TitleItem>
                    </TitleList>
                    <div>
                      <IconBtn
                        type="button"
                        onClick={() => setClickedOrder(_id)}
                      >
                        <UilEye />
                      </IconBtn>
                    </div>
                  </Item>
                  {clickedOrder === _id && (
                    <WrapperDesc>
                      <Table>
                        <THead></THead>
                        <tbody>
                          <tr>
                            <TD>': </TD>
                            <TD>{firstName}</TD>
                          </tr>
                          <tr>
                            <TD>: </TD>
                            <TD>{lastName}</TD>
                          </tr>
                          <tr>
                            <TD>: </TD>
                            <TD>{phone}</TD>
                          </tr>
                        </tbody>
                      </Table>
                      <Table>
                        <THead></THead>
                        <tbody>
                          <tr>
                            <TD>: </TD>
                            <TD>
                              {payment === 'receiving'
                                ? ' '
                                : ' '}
                            </TD>
                          </tr>
                          <tr>
                            <TD> : </TD>
                            <TD>
                              {(() => {
                                switch (deliveryType) {
                                  case 'novaPoshta':
                                    return ' ';
                                  case 'selfPickup':
                                    return '';
                                  case 'courier':
                                    return "'";
                                  default:
                                    return '';
                                }
                              })()}
                            </TD>
                          </tr>
                          <tr>
                            <TD>: </TD>
                            <TD>{city}</TD>
                          </tr>
                          <tr>
                            <TD>: </TD>
                            <TD>{department}</TD>
                          </tr>
                          <tr>
                            <TD>: </TD>
                            <TD>{address}</TD>
                          </tr>
                        </tbody>
                      </Table>
                      <Table>
                        {products &&
                          products.map(
                            ({ name, grind, quantity, weight, price }, i) => (
                              <Fragment>
                                <THead> {i + 1}</THead>
                                <tbody>
                                  <tr>
                                    <TD>: </TD>
                                    <TD>{name}</TD>
                                  </tr>
                                  <tr>
                                    <TD>: </TD>
                                    <TD>{quantity}</TD>
                                  </tr>
                                  <tr>
                                    <TD>: </TD>
                                    <TD>{grind}</TD>
                                  </tr>
                                  <tr>
                                    <TD>: </TD>
                                    <TD>{weight}</TD>
                                  </tr>
                                  <tr>
                                    <TD>: </TD>
                                    <TD>{price}</TD>
                                  </tr>
                                </tbody>
                                <br />
                              </Fragment>
                            )
                          )}
                      </Table>
                    </WrapperDesc>
                  )}
                </Fragment>
              );
            }
          )}
      </List>
    </Wrapper>
  );
}
